*{
    box-sizing: border-box;
}
body{
    background-color: #eff3f7;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
    margin: 0;
    padding: 0;
    height: 100%;
}
html,
body,
#root,
.container{
height: 100%;
display: flex;
justify-content: center;
}
.loader{
    width: 100%;
    height: 100%;
     display: flex;
     justify-content: center;
     font-weight: 300;
}